<template>
    <div>
        <AppHeaderManager />
        <!-- //header  -->

        <div class="mypage-container">
            <div class="mypage-content-side">
                <div class="mypage-profile">
                    <img src="https://image.ohou.se/i/bucketplace-v2-development/uploads/default_images/avatar.png?gif=1&w=240&h=240&c=c&webp=1"
                        alt=" ">
                    <h2>{{ userData.name }}</h2>
                </div>

                <div class="mypage-user-info">
                    <h2> </h2>
                    <p>: {{ userData.email }}</p>
                    <p>: {{ userData.hp }}</p>
                    <p>: {{ userData.address }}</p>
                    <a href="#" class="mypage-btn"> </a>
                </div>
            </div>

            <div class="mypage-content-main">
                <div class="mypage-order-history">
                    <h3> </h3>
                    <ul>
                        <li v-for="order in orderHistory" :key="order.orderNo">
                            {{ order.orderDate }} - : {{ order.productName }}, : {{ order.price }}, : {{
                        order.color }}
                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <AppFooter />
        <!-- //footer  -->
    </div>
</template>

<script>
import "@/assets/css/user/mypage.css";
import AppHeaderManager from '@/components/AppHeaderManager.vue';
import AppFooter from '@/components/AppFooter.vue';
import axios from 'axios';

export default {
    name: "MyPageView",
    components: {
        AppHeaderManager,
        AppFooter
    },
    data() {
        return {
            userData: {},
            orderHistory: []
        };
    },
    methods: {
        fetchData() {
            axios({
                method: 'get',
                url: `${this.$store.state.apiBaseUrl}/home/mypage/userinfo?userNo=${this.$store.state.userNo}`, //  
                headers: { "Content-Type": "application/json; charset=utf-8" },
                responseType: 'json'
            }).then(response => {
                this.userData = response.data; //  


            }).catch(error => {
                console.log(error);
            });
        },
        
        fetchData2() {
            axios({
                method: 'get',
                url: `${this.$store.state.apiBaseUrl}/home/mypage/orders?userNo=${this.$store.state.userNo}`, //  
                headers: { "Content-Type": "application/json; charset=utf-8" },
                responseType: 'json'
            }).then(response => {
                this.orderHistory = response.data;

            }).catch(error => {
                console.log(error);
            });
        }
    },
    created() {
        this.fetchData();
        this.fetchData2();
    }
};
</script>
