<template>
    <div class="wrap">
        <AppHeaderManager />

        <div class="content">
            <div class="side">
                <ul>
                    <li @click="toggleCategories">
                        <strong><router-link to="/manager/productlist"></router-link></strong>
                        <span v-if="showCategories">  </span>
                        <span v-else> </span> <!--   -->
                    </li>
                    <ul v-show="showCategories"> <!--   -->
                        <li class="cate-li"><strong @click="getList('recent')">  </strong></li>
                        <li class="cate-li">
                            <ul>
                                <li @click="toggleCategories00">
                                    <strong></strong>
                                    <span v-if="showCategories00">  </span>
                                    <span v-else> </span> <!--   -->
                                </li>
                                <ul v-show="showCategories00"> <!--   -->
                                    <li class="cate-li" v-on:click.prevent="showProductList('bed')"><a href=""></a>
                                    </li>
                                    <li class="cate-li" v-on:click.prevent="showProductList('mattress')"><a
                                            href=""></a></li>
                                    <li class="cate-li" v-on:click.prevent="showProductList('frame')"><a href="">
                                            </a></li>
                                </ul>
                            </ul>
                        </li>
                        <li class="cate-li">
                            <ul>
                                <li @click="toggleCategories02">
                                    <strong></strong>
                                    <span v-if="showCategories02">  </span>
                                    <span v-else> </span> <!--   -->
                                </li>
                                <ul v-show="showCategories02"> <!--   -->
                                    <li class="cate-li" v-on:click.prevent="showProductList('normal-sofa')"><a
                                            href=""> </a></li>
                                    <li class="cate-li" v-on:click.prevent="showProductList('lounge-sofa')"><a
                                            href=""> </a></li>
                                    <li class="cate-li" v-on:click.prevent="showProductList('single-sofa')"><a
                                            href="">1 </a></li>
                                </ul>
                            </ul>
                        </li>
                        <li class="cate-li">
                            <ul>
                                <li @click="toggleCategories03">
                                    <strong></strong>
                                    <span v-if="showCategories03">  </span>
                                    <span v-else> </span> <!--   -->
                                </li>
                                <ul v-show="showCategories03"> <!--   -->
                                    <li class="cate-li" v-on:click.prevent="showProductList('dining-table')"><a
                                            href=""></a></li>
                                    <li class="cate-li" v-on:click.prevent="showProductList('side-table')"><a
                                            href=""> </a></li>
                                </ul>
                            </ul>
                        </li>
                        <li class="cate-li">
                            <ul>
                                <li @click="toggleCategories04">
                                    <strong></strong>
                                    <span v-if="showCategories04">  </span>
                                    <span v-else> </span> <!--   -->
                                </li>
                                <ul v-show="showCategories04"> <!--   -->
                                    <li class="cate-li" v-on:click.prevent="showProductList('closet')"><a href=""></a>
                                    </li>
                                    <li class="cate-li" v-on:click.prevent="showProductList('hanger')"><a href=""></a>
                                    </li>
                                    <li class="cate-li" v-on:click.prevent="showProductList('built-in')"><a
                                            href=""></a></li>
                                </ul>
                            </ul>
                        </li>
                    </ul>
                    <li @click="toggleCategories05">
                        <strong> </strong>
                        <span v-if="showCategories05">  </span>
                        <span v-else> </span>
                    </li>
                    <ul v-show="showCategories05">
                        <li class="cate-li"><router-link to="/manager/chart"></router-link></li>
						<li class="cate-li"><router-link to="/manager/state"></router-link></li>
                    </ul>
                </ul>
            </div>
            <div class="main">
                <div class="insert">
                    <router-link to="/manager/productinsert">
                        <button class="insert-button"> </button>
                    </router-link>
                    <div class="dropdown">
                        <button class="dropbtn" id="sortButton"></button>
                        <div class="dropdown-content">
                            <a href="#" @click="changeSort('recent')"></a>
                            <a href="#" @click="changeSort('review')"></a>
                            <a href="#" @click="changeSort('rating')"></a>
                        </div>
                    </div>
                </div>
                <span class="manage-products"> </span>
                <div class="bed">
                    <h3><br> </h3>
                    <div class="product" v-bind:key="i" v-for="(productVo, i) in productList">
                        <img class="main-img"
                            v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${productVo.main_img}`" alt="">
                        <div class="product-info">
                            <span class="product-name">{{ productVo.product_name }}</span>
                            <div class="review-manager">
                                <span v-for="(star, i) in 5" :key="i" class="star">
                                    <img v-if="productVo.avg_star > i" src="@/assets/images/homedeco/star.png" alt="">
                                    <img v-else src="@/assets/images/homedeco/star2.png" alt="">
                                </span>
                            </div>
                            <span class="product-rating">{{ productVo.avg_star }}</span>
                            <form v-on:submit.prevent="remove(productVo.product_no)" action="" method="">
                                <button class="delete-button"></button>
                            </form>
                        </div>
                        <br>
                    </div>
                </div>
            </div>
        </div>

        <AppFooter />

    </div>
</template>


<script>
import "@/assets/css/managerY/productLIst.css"
import AppHeaderManager from '@/components/AppHeaderManager.vue';
import AppFooter from '@/components/AppFooter.vue';
import axios from 'axios';

export default {
    name: "ProductListView",
    components: {
        AppHeaderManager,
        AppFooter
    },
    data() {
        return {
            showCategories: false,
            showCategories00: false,
            showCategories02: false,
            showCategories03: false,
            showCategories04: false,
            showCategories05: false,
            productList: [],
            productVo: {
                product_no: '',
                title: '',
                price: '',
                category: '',
                main_img: '',
                avg_star: ''
            },

        };
    },
    mounted() {
        document.getElementById('sortButton').innerText = '';

        var dropdownItems = document.querySelectorAll('.dropdown-content a');
        dropdownItems.forEach(function (item) {
            item.addEventListener('click', function () {
                //var sortType = this.getAttribute('data-sort');
                document.getElementById('sortButton').innerText = this.innerText;
                
            });
        });
    },
    methods: {
        changeSort(sortType) {
            document.getElementById('sortButton').innerText = sortType;
            this.getList(sortType);
        },
        toggleCategories() {
            this.showCategories = !this.showCategories; //      
        },
        toggleCategories00() {
            this.showCategories00 = !this.showCategories00; //      
        },
        toggleCategories02() {
            this.showCategories02 = !this.showCategories02; //      
        },
        toggleCategories03() {
            this.showCategories03 = !this.showCategories03; //      
        },
        toggleCategories04() {
            this.showCategories04 = !this.showCategories04; //      
        },
        toggleCategories05() {
            this.showCategories05 = !this.showCategories05; //      
        },
        getList(sortType = 'recent') {
            console.log(" ");

            axios({
                method: 'get',
                url: `${this.$store.state.apiBaseUrl}/home/manager/list`,
                headers: { "Content-Type": "application/json; charset=utf-8" },
                params: { sortType: sortType },
                responseType: 'json'
            }).then(response => {
                console.log(response.data.apiData);
                this.productList = response.data.apiData;
            }).catch(error => {
                console.log(error);
            });
        },
        remove(product_no) {
            axios({
                method: 'delete',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/home/manager/delete/` + product_no,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                responseType: 'json' //
            }).then(response => {
                console.log(response.data); //
                this.productList = this.productList.filter(item => item.product_no !== product_no);
            }).catch(error => {
                console.log(error);
            });
        },
        showProductList(category) {
            console.log(": " + category);
            axios({
                method: 'post',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/home/manager/categorylist`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: { category: category },
                responseType: 'json' //
            }).then(response => {
                console.log(response.data.apiData); //
                this.productList = response.data.apiData;
            }).catch(error => {
                console.log(error);
            });
        },
    },
    created() {
        this.getList('recent'); //   
    }
};
</script>
