<template>
    <div class="login-container">

        <div class="login-logo">
            <img src="../../assets/images/homedeco/homelogo.png" alt="">
            <div class="login-brand-text"></div>
        </div>

        <form id="loginForm" @submit.prevent="login">
            <div class="login-form-group">
                <label for="id"></label>
                <input v-model="loginVo.id" type="text" id="username" name="username" placeholder=" " required>
            </div>
            <div class="login-form-group">
                <label for="password"></label>
                <input v-model="loginVo.password" type="password" id="password" name="password"
                    placeholder=" " required>
            </div>
            <button type="submit" class="login-btn"></button>
            <!--     -->
            <div v-if="loginFailed" class="login-failed-message"> .  .</div>
        </form>



    </div>
</template>




<script>
import "@/assets/css/user/login.css";
import axios from 'axios';

export default {
    name: "LoginView",
    components: {
    },
    data() {
        return {
            loginVo: {
                id: "",
                password: ""
            },
            loginFailed: false, //     ,
            user_no: '',
            cartCount: ''
        };
    },
    methods: {
        login() {
            console.log("");

            axios({
                method: 'post', // put, post, delete
                url: `${this.$store.state.apiBaseUrl}/home/login`,
                headers: { // + 
                    "Content-Type": "application/json; charset=utf-8",
                    "Authorization": "Bearer " + this.$store.state.token
                },
                data: this.loginVo,

                responseType: 'json' //
            }).then(response => {
                if (response.data.result == "success") {

                    //    
                    console.log(response); //
                    console.log(response.data); // User

                    //  
                    let authUser = response.data.apiData;

                    //token    
                    //"Authorization, Bearer  + token"
                    const token = response.headers.authorization.split(" ")[1]

                    //vuex
                    this.$store.commit("setAuthUser", authUser);
                    this.$store.commit("setToken", token);

                    console.log(authUser);
                    console.log(token);
                    console.log(" :", this.$store.state.token);

                    //      
                    this.cart(authUser.userNo);
                    this.$router.push("/");
                } else {
                    //   
                    this.loginFailed = true;
                }
            }).catch(error => {
                //    
                console.error(' :', error);
                this.loginFailed = true;

                //   
                console.log(" .  :", this.loginVo.id, this.loginVo.password);

                //  
                this.loginVo.id = "";
                this.loginVo.password = "";

            });

            console.log(" :", this.loginVo.id, this.loginVo.password);
        },
        cart(userNo) {
            axios({
                method: 'get',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/home/cartcount`,
                headers: { "Content-Type": "application/json; charset=utf-8", "Authorization": "Bearer " + this.$store.state.token }, //
                params: { user_no: userNo },
                responseType: 'json' //
            }).then(response => {
                console.log("============================");
                console.log(response.data.apiData); //
                this.cartCount = response.data.apiData;
                this.$store.commit("setCartCount", response.data.apiData);
            }).catch(error => {
                console.log(error);
            });
        }
    },
};
</script>
