<template>
	
<div id="wrap">

	<AppHeader @update="catchKeyword"/>
	<!-- //header  -->

	<AppNavView @list="category"/>
    <!-- //nav  -->

	<div class="ss-category clearfix">
		<div class="ss-side">
			<ul>
				<li @click="toggleCategories">
					<strong></strong>
					<span v-if="showCategories">  </span>
					<span v-else> </span> <!--   -->
				</li>
				<ul v-show="showCategories"> <!--   -->
					<li class="cate-li" v-on:click.prevent="showProductList('bed')"><a></a></li>
					<li class="cate-li" v-on:click.prevent="showProductList('mattress')"><a href=""></a></li>
					<li class="cate-li" v-on:click.prevent="showProductList('frame')"><a href=""></a></li>
				</ul>
			</ul>
			<ul>
				<li @click="toggleCategories02">
					<strong></strong>
					<span v-if="showCategories02">  </span>
					<span v-else> </span> <!--   -->
				</li>
				<ul v-show="showCategories02"> <!--   -->
					<li class="cate-li" v-on:click.prevent="showProductList('normal-sofa')"><a href=""> </a></li>
					<li class="cate-li" v-on:click.prevent="showProductList('lounge-sofa')"><a href=""> </a></li>
					<li class="cate-li" v-on:click.prevent="showProductList('single-sofa')"><a href="">1 </a></li>
				</ul>
			</ul>
			<ul>
				<li @click="toggleCategories03">
					<strong></strong>
					<span v-if="showCategories03">  </span>
					<span v-else> </span> <!--   -->
				</li>
				<ul v-show="showCategories03"> <!--   -->
					<li class="cate-li" v-on:click.prevent="showProductList('dining-table')"><a href="" ></a></li>
					<li class="cate-li" v-on:click.prevent="showProductList('side-table')"><a href=""></a></li>
				</ul>
			</ul>
			<ul>
				<li @click="toggleCategories04">
					<strong></strong>
					<span v-if="showCategories04">  </span>
					<span v-else> </span> <!--   -->
				</li>
				<ul v-show="showCategories04"> <!--   -->
					<li class="cate-li" v-on:click.prevent="showProductList('closet')"><a href=""></a></li>
					<li class="cate-li" v-on:click.prevent="showProductList('hanger')"><a href=""></a></li>
					<li class="cate-li" v-on:click.prevent="showProductList('built-in')"><a href=""></a></li>
				</ul>
			</ul>
			
		</div>
		<!-- content-side -->

		<div class="ss-main">
            <div class="ss-3btn">
                <div class="dropdown">
                        <button class="dropbtn" id="sortButton"></button>
                        <div class="dropdown-content">
                            <a href="#" @click="changeSort('recent')"></a>
                            <a href="#" @click="changeSort('review')"></a>
                            <a href="#" @click="changeSort('rating')"></a>
                        </div>
                    </div>
            </div>
            <div class="ss-goods" v-for="(list,i) in list" v-bind:key="i">
                <img v-on:click="push" :data-no="`${list.productNo}`" v-bind:src="`${this.$store.state.apiBaseUrl}/upload/${list.mainImg}`"><br>
                <span>{{ list.category }}</span><br>
                <span> : {{ list.productName }}</span><br>
                <span> : {{ list.star }}</span>&nbsp;
                <span> : {{ Number(list.price).toLocaleString('ko-KR') }} </span>
            </div>
		</div>
		<!-- content-main -->
	</div>
	<!-- //content  -->

	<AppFooter />
	<!-- //footer  -->

</div>
<!-- //wrap -->

</template>

<script>
import '@/assets/css/main/ss-home.css';
import '@/assets/css/main/ss-category.css';
import AppHeader from '@/components/AppHeader.vue';
import AppNavView from '@/components/AppNavView.vue';
import AppFooter from '@/components/AppFooter.vue';
import axios from 'axios';

export default {
	name: "CategoryView",
	components: {
		AppHeader,
        AppNavView,
		AppFooter
	},
	data() {
		return {
			show : false, //
			showCategories: false,
            showCategories02: false,
			showCategories03: false,
            showCategories04: false,
            hitList : [
                '' ,
                '',
                '' ,
                '' ,
                 '' ,
                '' ,
                '',
                '' ,
                '' ,
                '' 
            ],
			list :this.$store.state.category,
			keyword : ''
		};
	},
	methods: {
		//    
		mouseover(){
			this.show = true; 
		},
		//    
        mouseleave(){ 
			this.show = false;
		},

        //  
		toggleCategories() {
            this.showCategories = !this.showCategories; //      
        },
        toggleCategories02() {
            this.showCategories02 = !this.showCategories02; //      
        },
        toggleCategories03() {
            this.showCategories03 = !this.showCategories03; //      
        },
        toggleCategories04() {
            this.showCategories04 = !this.showCategories04; //      
        },
        ///////////////////////////////////////
        category(category){
            this.$store.commit('setCategory',category);
            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/home/main/category`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {keyword : category}, //get   
                // data: , //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                if(response.data.result == 'success'){
                    this.list = response.data.apiData ;
                } else {
                    alert(" .")
                }
            }).catch(error => {
                console.log(error);
            });
        },
        catchKeyword(keyword){
            keyword = this.$store.state.category;
            // console.log(keyword +'1' )
            // if(typeof keyword === 'undefined'){
            //     keyword = 'bed'
            //     console.log(keyword +'2')
            // } else {
            //     keyword = this.$store.state.category;
            // } 

            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/home/main/category`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {keyword : keyword}, //get   
                // data: , //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {
                if(response.data.result == 'success'){
                    this.list = response.data.apiData ;
                } else {
                    alert(" .")
                }
            }).catch(error => {
                console.log(error);
            });
            
        },

        /////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        showProductList(category){
            // console.log(category);
            axios({
                    method: 'get', // put, post, delete 
                    url: `${this.$store.state.apiBaseUrl}/home/main/category`,
                    headers: { "Content-Type": "application/json; charset=utf-8" }, //
                    params: {keyword : category}, //get   
                    // data: , //put, post, delete   JSON  
                    responseType: 'json' //
                }).then(response => {
                    if(response.data.result == 'success'){
                        this.$store.commit('setCategory',null);
                        this.$store.commit('setCategory', response.data.apiData);
                        this.list = this.$store.state.category;
                        
                    } else {
                        alert(" .");
                    }
                }).catch(error => {
                    console.log(error);
                });
        },
        push(event){
            let no =event.target.dataset.no
            this.$router.push('/product/'+ no);
        },

        ////////////////////////////////////  ///////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
        changeSort(sortType) {
            document.getElementById('sortButton').innerText = sortType;
            let word = this.list[0].category
            console.log(word);
        }
	},
    mounted(){
        document.getElementById('sortButton').innerText = '';
        
        let dropdownItems = document.querySelectorAll('.dropdown-content a');
        dropdownItems.forEach(function (item) {
            item.addEventListener('click', function () {
                document.getElementById('sortButton').innerText = this.innerText;
            });
        });
    },
	
	created(){
        this.toggleCategories();
        this.catchKeyword();
    }
};
</script>

<style></style>