<template>
    <div>
        <!--  -->
        <AppHeaderManager />

        <!--     -->
        <div class="pay-container">
            <div class="pay-content">
                <form id="pay-orderForm">
                    <!--   -->
                    <div class="pay-section">
                        <label for="shipping_info"></label>
                        <div class="pay-change-info" @click="resetShipping"> </div>
                        <input type="text" id="shipping_info" name="shipping_info" placeholder="  "
                            v-model="this.$store.state.authUser.address">
                        <select id="shipping_info_select" name="shipping_info_select" v-model="shippingOption">
                            <option value="default">  </option>
                            <option value="front_door">   </option>
                            <option value="security">  </option>
                            <option value="contact">  </option>
                            <option value="custom"></option>
                        </select>
                    </div>

                    <!--   -->
                    <div class="pay-section">
                        <label for="customer_info"><strong></strong></label>
                        <div class="pay-change-info" @click="resetCustomerInfo">  </div>
                        <input type="text" id="customer_info" name="customer_info" placeholder="  "
                            v-model="this.$store.state.authUser.name">
                    </div>
                    <!--    -->
                    <div class="pay-product-list section">
                        <label for="products"><strong> </strong></label>
                        <ul id="products">
                            <li v-for="(product, index) in this.$store.state.nowOrderList" :key="index">
                                <img :src="product.image" :alt="product.name">
                                <div class="pay-name">: {{ product.product }}</div>
                                <div class="pay-price">: {{ Number(product.price).toLocaleString('ko-KR') }}</div>
                                
                                <div class="pay-color">: {{ product.color }}</div>
                                <div class="pay-count">: {{ product.count }}</div> <!--    -->
                            </li>
                        </ul>
                    </div>

                    <!--   -->
                    <label for="payment_method"><strong> </strong></label>
                    <br><br>

                    <div class="pay-payment-methods">
                        <label v-for="(method, index) in paymentMethods" :key="index" :for="'pay-' + method"
                            @click="selectPaymentMethod(method)" :class="{ active: method === selectedPaymentMethod }">
                            <img :src="paymentMethodImages[method]" :alt="method">
                            <div>{{ method }}</div>
                        </label>
                    </div>
                </form>
            </div>

            <!--    -->
            <div class="pay-sidebar">
                <div class="pay-sidebar-content">
                    <h3>  </h3>
                    <br>
                    <p> : {{ Number(this.$store.state.totalPrice).toLocaleString('ko-KR') }}</p>
                    
                    <br>
                    <p>: {{ shippingFee }}</p>
                    <br>
                    <hr>
                    <br>
                    <p> : {{ Number(this.$store.state.totalPrice).toLocaleString('ko-KR') }}</p>
                    
                    <br><br>
                    <div class="pay-agreement">
                        <input type="checkbox" id="pay-agree" name="agree" v-model="agreed">
                        <label for="pay-agree">   .</label><br>
                        <p style="font-size: 14px;">  <a href="#"></a>  .</p>
                    </div>

                    <button class="pay-btn" @click="completePayment"> </button>
                </div>
            </div>
        </div>

        <!--  -->
        <AppFooter />
    </div>
</template>

<script>
import axios from 'axios';
import "@/assets/css/user/pay.css";
import AppHeaderManager from '@/components/AppHeaderManager.vue';
import AppFooter from '@/components/AppFooter.vue';
import { mapState, mapMutations } from 'vuex'; 

export default {
    name: "PayView",
    components: {
        AppHeaderManager,
        AppFooter
    },
    data() {
        return {
            shippingInfo: {}, //  
            shippingOption: "default",
            customerInfo: "", //  
            userNo: 1,
            products: [ this.$store.state.nowOrderList ],
            paymentMethods: [" ", " ", "", "", ""],
            paymentMethodImages: {
                " ": "https://image.ohou.se/i/bucketplace-v2-development/pg/168311599350498640.png?w=108&h=108&c=c",
                " ": "https://image.ohou.se/i/bucketplace-v2-development/pg/168311600677152970.png?w=108&h=108&c=c",
                "": "https://image.ohou.se/i/bucketplace-v2-development/pg/168311602265893776.png?w=108&h=108&c=c",
                "": "https://image.ohou.se/i/bucketplace-v2-development/pg/168311600097145245.png?w=108&h=108&c=c",
                "": "https://image.ohou.se/i/bucketplace-v2-development/pg/168311601701037520.png?w=108&h=108&c=c"
            },
            agreed: false,
            selectedPaymentMethod: null
          
        };
    },
    computed: {
        ...mapState(['nowOrderList']), 

        shippingFee() {
            return "";
        },
        
    },
    methods: {
        ...mapMutations(['setNowPayment']), // mapMutations  

        resetShipping() {
            this.shippingInfo = "";
        },
        resetCustomerInfo() {
            this.customerInfo = "";
        },
        selectPaymentMethod(method) {
            console.log("  :", method);
            this.selectedPaymentMethod = method;
        },
        completePayment() {
            if (this.agreed) {
                let tmp ={
                    userNo: this.userNo,
                    orderList:  this.$store.state.nowOrderList
                }
                console.log(tmp);
                axios({
                    method: 'post',
                    url: `${this.$store.state.apiBaseUrl}/home/pay/createOrder`,
                    headers: { "Content-Type": "application/json; charset=utf-8" },
                    data: tmp,
                    responseType: 'json'

                }).then(response => {
                    if(response)
                    console.log("   :", response.data);
                    this.createOrder = response.data.apiData;

                    alert(" .");

                    this.$router.push("/payend/"+this.createOrder);

                }).catch(error => {
                    console.log(error);
                });
            } else {
                alert(" !");
            }
        },
        

    },
    created() {
        const userNoFromSession = sessionStorage.getItem('userNo');
        if (userNoFromSession) {
            this.userNo = parseInt(userNoFromSession);
        } else {
            //     
        }
    

        // nowOrderList   shippingInfo customerInfo 
        if (this.$store.state.nowOrderList !== null) {
            const order = this.$store.state.nowOrderList;
            this.shippingInfo = order.shippingInfo;
            this.customerInfo = order.customerInfo;
        }
    }
};
</script>