<template>
    <div class="wrap">
        <AppHeaderManager />

        <div class="content">
            <div class="main-insert">
                <form class="form-insert" v-on:submit.prevent="insertProduct" enctype="multipart/form-data">
                    <div id="additional-files-wrapper" class="flex-container">

                        <div class="left">
                            <div class="preview-image">
                                <img :src="previewImage" style="max-width: 300px; max-height: 300px;">
                            </div>

                            <div class="input">
                                <input type="file" id="profile" name="profile" class="file-input" style="width: auto;"
                                    @change="handleImageChange">
                            </div>
                        </div>

                        <div class="right">
                            <div class="input">
                                <label for="title">:</label>
                                <input type="text" id="title" name="title" v-model="title">
                            </div>

                            <div class="input">
                                <label for="price">&nbsp;&nbsp;&nbsp;:</label>
                                <input type="text" id="price" name="price" v-model="price"> 
                            </div>

                            <div class="input">
                                <select id="category-select" @change="handleCategoryChange">
                                    <option value="" selected disabled hidden> </option>
                                    <option value="bed"></option>
                                    <option value="sofa"></option>
                                    <option value="table"></option>
                                    <option value="closet"></option>
                                </select>
                            </div>

                            <div id="subCategoryBed" class="input" v-show="selectedCategory === 'bed'">
                                <select id="subCategoryBedSelect" @change="selectCategory">
                                    <option value="" selected disabled hidden>  </option>
                                    <option v-bind:value="category[0]"></option>
                                    <option v-bind:value="category[1]"></option>
                                    <option v-bind:value="category[2]"></option>
                                </select>
                            </div>

                            <div id="subCategorySofa" class="input" v-show="selectedCategory === 'sofa'">
                                <select id="subCategorySofaSelect" @change="selectCategory">
                                    <option value="" selected disabled hidden>  </option>
                                    <option v-bind:value="category[3]"></option>
                                    <option v-bind:value="category[4]"></option>
                                    <option v-bind:value="category[5]">1 </option>
                                </select>
                            </div>

                            <div id="subCategoryTable" class="input" v-show="selectedCategory === 'table'">
                                <select id="subCategoryTableSelect" @change="selectCategory">
                                    <option value="" selected disabled hidden>  </option>
                                    <option v-bind:value="category[6]"></option>
                                    <option v-bind:value="category[7]"> </option>
                                </select>
                            </div>

                            <div id="subCategoryCloset" class="input" v-show="selectedCategory === 'closet'">
                                <select id="subCategoryClosetSelect" @change="selectCategory">
                                    <option value="" selected disabled hidden>  </option>
                                    <option v-bind:value="category[8]"></option>
                                    <option v-bind:value="category[9]"></option>
                                    <option v-bind:value="category[10]"></option>
                                </select>
                            </div>

                            <div class="input-attach">
                                <label for="add">  :&nbsp;</label>
                                <input type="file" id="add" name="add" @change="addContentFile" class="file-input">

                                <button type="button" class="add-file-button" @click="addFileInput"> </button>
                            </div>
                        </div>
                    </div>

                    <button type="submit" class="insert01"></button>
                </form>
            </div>
        </div>

        <AppFooter />
    </div><!-- wrap -->
</template>

<script>
import "@/assets/css/managerY/productInsert.css"
import AppHeaderManager from '@/components/AppHeaderManager.vue';
import AppFooter from '@/components/AppFooter.vue';
import axios from 'axios';

export default {
    name: "ProductInsertView",
    components: {
        AppHeaderManager,
        AppFooter
    },
    data() {
        return {
            selectedCategory: '',
            previewImage: require('@/assets/images/managerY/img.jpg'),
            profile: '',
            title: '',
            price: '',
            category: ["bed", "mattress", "frame", "normal-sofa", "lounge-sofa", "single-sofa", "dining-table", "side-table", "closet", "hanger", "built-in"],
            add: [],
            selectCate: ""
        };
    },
    methods: {
        handleCategoryChange(event) {
            this.selectedCategory = event.target.value;
        },
        addContentFile(event) {
            this.add.push(event.target.files[0]);
        },
        addFileInput() {
            var rightDiv = document.querySelector('.right'); // right div .
            var fileInputContainer = document.createElement('div');
            fileInputContainer.className = 'input-attach';

            var fileInput = document.createElement('input');
            fileInput.type = 'file';
            fileInput.name = 'add';
            fileInput.className = 'file-input';
            fileInput.multiple = true;
            fileInput.addEventListener('change', (event) => {
                for (let i = 0; i < event.target.files.length; i++) {
                    this.add.push(event.target.files[i]); //  Vue  add  
                }
            });

            var removeButton = document.createElement('button');
            removeButton.type = 'button';
            removeButton.className = 'remove-file-button';
            removeButton.textContent = ' ';
            removeButton.onclick = function () {
                rightDiv.removeChild(fileInputContainer); // right div  fileInputContainer .
            };

            fileInputContainer.appendChild(fileInput);
            fileInputContainer.appendChild(removeButton);
            rightDiv.appendChild(fileInputContainer); // right div  fileInputContainer .

        },

        //     
        handleImageChange(event) {
            //  
            this.profile = event.target.files[0];

            // FileReader    
            const reader = new FileReader();

            //      
            reader.onload = (e) => {
                //    previewImage    
                this.previewImage = e.target.result;
            };

            //  
            if (this.profile) {
                reader.readAsDataURL(this.profile);
            }
        },
        selectCategory(event) {
            console.log(event.target.value);
            this.selectCate = event.target.value;
        },
        insertProduct() {
            console.log("aa");
            console.log(this.add);
            let formData = new FormData();


            for (let i = 0; i < this.add.length; i++) {
                formData.append('contentFiles', this.add[i]); //  Vue  add  
            }

            //
            formData.append('title', this.title);
            formData.append('price', this.price);
            formData.append('category', this.selectCate);

            //
            formData.append('profile', this.profile);

            //
            // formData.append('contentFiles', JSON.stringify(this.add));

            axios({
                method: 'post',
                url: `${this.$store.state.apiBaseUrl}/home/manager/insert`,
                headers: { "Content-Type": "multipart/form-data" },
                data: formData,
                responseType: 'json'
            }).then(response => {
                console.log(response);
                console.log(response.data.apiData);
                this.$router.push('/manager/productlist')
            }).catch(error => {
                console.log(error);
            });
        }


    }
};
</script>